.top-bar {
  padding-block: 24px;
  display: flex;
  justify-content: space-between ;
  align-items: center;

  @include on-tablet {
    padding-block: 32px;

  }

  &__icons {
    display: flex;
    gap: 24px;

    @include on-tablet {
      gap: 32px;
    }
  }

  &__logo {
    display: flex;
  }

}
